<template lang="pug">
  .container.header__container
    .header__content
      router-link(
      to='/'
      ).header__logo 
        img(src="../assets/images/logo.png" class="header__logo-img")
        .header__title Planet9
    router-link(
      v-if="user.role.role=='ROLE_TEACHER'"
      to='/teacher/profile'
      ).user-info {{user.lastName}} {{user.firstName}}
    router-link(
      v-if="user.role.role=='ROLE_SCHOLAR'"
      to='/scholar/profile'
      ).user-info {{user.lastName}} {{user.firstName}}
    .header__quit-link
      a.quit-link(@click="logoutUser") Выход
</template>
<script>
import { mapState, mapActions } from "vuex";
import router from '../router';
export default {
  computed: {
    ...mapState ("user", {
        user: state => state.user})
  },
  methods: {      
    ...mapActions("user", ["logout"]),
    logoutUser(){
      localStorage.removeItem("token");
      this.logout()
      router.replace('/login')
    }
  }
}
</script>
